.Button {
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  cursor: pointer;
  font: inherit;
  padding: 10px;
  margin: 10px;
  font-weight: bold;
  text-align: center;

  &.-primary {
    background-color: var(--color-blue);
    color: var(--color-white);
    border-radius: var(--standard-border-radius);
  }

  &.-secondary {
    color: var(--color-white);
    border-radius: var(--standard-border-radius);
    background-color: var(--color-green);
  }

  // SIZES
	// height
	&.-large {
		line-height: 1;
		padding-bottom: 19px;
		padding-top: 19px;
	}

	&.-small {
		font-size: 12px;
		padding-bottom: 9px;
		padding-top: 9px;
	}

	// width
	&.-wide {
		padding-left: 10px;
		padding-right: 10px;
		width: 100%;
	}

	&.-short {
		padding-left: 20px;
		padding-right: 20px;
	}
}

.Button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

