.Card {
    min-width: 150px;
    min-height: 50px;
    margin: 5px;
    box-shadow: 0 2px 3px #eee;
    background: #fff;
    padding: 10px 20px;
    cursor: pointer;
    text-align: center;
  }
  
  .Card.-selected {
    border: 2px solid blue;
  }
  
  .Card.-match {
    position: relative;
    animation: hideMatchedCards 1.7s ease forwards;
  }
  
  @keyframes hideMatchedCards {
    from {
      left: 0;
    }
    to {
      left: -200%;
    }
  }
  