.FillGaps {
    box-shadow: 2px 3px 14px 0px #000;
    background-color: #fff;
    max-width: 648px;
    margin: auto;
    padding: 30px;
}
.FillGaps ol {
    list-style: decimal;
}
.FillGaps ol li {
    margin-bottom: 10px;
}
.FillGaps ol li.-correct::after,
.FillGaps ol li.-wrong::after {
    content: '';
    display: inline-block;
    width: 30px;
    height: 30px;
    vertical-align: middle;
    margin-left: 10px;
}
.FillGaps ol li.-wrong::after {
    background: url(../../../../static_assets/images/wrong.png) center / cover;
}
.FillGaps ol li.-correct::after {
    background: url(../../../../static_assets/images/tick.png) center / cover;
}
.FillGaps ol li .InputContainer {
    width: 100px;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
}
.FillGaps ol li .InputContainer .Label {
    margin: 0;
}
.FillGapsControls {
    text-align: center;
}