.NavigationItem {
  margin: 10px 0;
  box-sizing: border-box;
  display: block;
  width: 100%;
  position: relative;
}

.NavigationItem a {
  color: #8f5c2c;
  text-decoration: none;
  width: 100%;
  box-sizing: border-box;
  display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
  color: #40a4c8;
}

@media (min-width: 500px) {
  .NavigationItem {
    margin: 0;
    display: flex;
    height: 100%;
    width: auto;
    align-items: center;
  }

  .NavigationItem a {
    color: white;
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4px solid transparent;
  }

  .NavigationItem a:hover,
  .NavigationItem a:active,
  .NavigationItem a.active {
    background-color: #8f5c2c;
    border-bottom: 4px solid #40a4c8;
    color: white;
  }
}
.NavigationItems > .NavigationItem:first-of-type a:hover {
  border-bottom: 4px solid red;
}
.NavigationItems > .NavigationItem:nth-of-type(2) a:hover {
  border-bottom: 4px solid blue;
}
.NavigationItems > .NavigationItem:nth-of-type(3) a:hover {
  border-bottom: 4px solid rgb(9, 231, 9);
}
.NavigationItems > .NavigationItem:nth-of-type(4) a:hover {
  border-bottom: 4px solid rgb(168, 128, 19);
}
.NavigationItems > .NavigationItem:last-of-type a:hover {
  border-bottom: 4px solid gold;
}

.NavigationItems {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-flow: column;
  align-items: center;
  height: 100%;
}

@media (min-width: 500px) {
  .NavigationItems {
    flex-flow: row;
  }
}

.submenu {
  display: none;
  position: absolute;
  top: 110%;
  right: 0;
  background: #999;
  width: 180%;
  text-transform: uppercase;
}

.submenu > li {
  display: block;
  border-bottom: 1px solid #000;
}

.has-submenu > a {
  padding-right: 25px;
}

.has-submenu::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 100%;
}

.has-submenu > a::after {
    content: "\25BC";
    position: absolute;
    color: #fff;
    top: 50%;
    right: 10px;
    font-size: 0.9rem;
    transform: translateY(-60%);
  }
  .has-submenu.-active > .submenu {
    display: block;
  }
  .has-submenu.-active > a::after {
    content: "\25B2";
  }

  @media (max-width: 500px) {
    .submenu {
      position: static;
      width: 100%;
      background: none;
      list-style: square;
    }
    .has-submenu::before {
      content: none;
    }
    .has-submenu > a::after {
      color: #8f5c2c;
      top: 0;
      transform: none;
    }
  }