:root {
	--main-color: #5faf4b;
	--font-pale: #9a9a9a;
	--font-bold: #0d0e10;
	--main-bg: #f3f3f3;
	--old-paper-bg: #efdfbb;
	--letters-bg: #59baf1;
	--grey-bg: #f6f6f6;
	--color-white: #fff;
	--color-blue: #2582e7;
	--color-grey: #bbb;
	--color-light-grey: #dadada;
	--color-grey-pale: #f4f6f7;
	--color-light-green: #ebf4fd;
	--color-green: #03ac13;
	--color-green-pale: #dcf5eb;
	--color-red:#ff2800;
	--color-red-pale: #fce4df;	
	--footer-bg: #37383c;
	--gradient: #87888d;
	--border-color: #e6e9ee;
	--font-h2-h3: 700 1.1rem / 1.2 "Roboto","Arial","Helvetica",sans-serif;
	--spacing-full: 32px;
	--spacing-half: calc(var(--spacing-full) / 2);
	--spacing-quarter: calc(var(--spacing-full) / 4);
	--standard-border-radius: 4px;
}

*, *::before, *::after {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

html, body {
	width: 100%;
	height: 100%;
	font-size: 16px;
	font-family: "Roboto","Arial","Helvetica",sans-serif;
}

.wrapper {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	min-height: 100%;
	background: var(--gradient);
}

main {
	flex-grow: 1; 
	/* to make footer always sticked to the bottom no matter eather there is content in main or no */
}

header, main, footer {
	width: 100%;
}

.wrap {
	max-width: 1280px;
	min-width: 320px;
	width: 100%;
	text-align: left;
	margin: 0 auto;
}

ul, ol, dl {
	list-style: none;
}

img {
	display: inline-block;
	max-width: 100%;
}

label {
	display: inline-block;
}

a,
button {
	cursor: pointer;
}

a {
	color: #4062b9;
	text-decoration: none;
	display: inline-block;

	&:hover {
		text-decoration: underline;
	}
}

.modal {
	width:100%; 
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba(0,0,0,.6);
	z-index: 100;
}

.clearfix::after {
	content: '';
	display: block;
	clear: both;
}

.mb-half {
	margin-bottom: var(--spacing-half);
}